<template>
    <div>
        <div class="bg" style="background: url(~/static/img/pilot/bg.jpg) no-repeat center center">
        </div>
        <grid-cell :pages="pages"></grid-cell>
    </div>
</template>

<script>
import GridCell from '../../components/GridCell';
import localjson from '../../util/localjson';

export default {
    name: 'pilot',
    components: {
        GridCell,
    },
    data() {
        return {
            pages:[
                { index:1, title: '引航计划', image: 'pilot/yhjh.png', path: 'pilot/yhjh', isValid: true },
                { index:2, title: '个人计划', image: 'pilot/grjh.png', path: 'pilot/grjh', isValid: true },
                { index:3, title: '艘次数据', image: 'pilot/scsj.png', path: 'pilot/scsj', isValid: true },
                { index:4, title: '积分核对', image: 'pilot/jfhd.png', path: 'pilot/jfhd', isValid: true },
                { index:5, title: '租艇信息', image: 'pilot/ztxx.png', path: 'pilot/ztxx', isValid: true },
                { index:6, title: '驻勤调度', image: 'pilot/zqdd.png', path: 'pilot/zqdd', isValid: true },
                { index:7, title: '特殊计划', image: 'pilot/tsjh.png', path: 'pilot/tsjh', isValid: false },
                { index:8, title: '规则规范', image: 'pilot/gzgf.png', path: 'pilot/gzgf', isValid: false },
                { index:9, title: '其他信息', image: 'pilot/qtxx.png', path: 'pilot/qtxx', isValid: false },
            ],
        };
    },
    methods: {
    },
    activated() {
        const currpage = {text: '引航信息', route: '/pilot'};
        localjson.setCurrentPage(currpage);
        this.$store.commit('setCurrentPage', currpage);

        this.$store.commit('setAppHeader', {
            show: true,
            title: '引航信息',
            showMenu: true,
            showBack: false,
            actions: [
            ]
        });
        this.$store.commit('setEnableSwipeOut', true);

    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom

.bg
    
    margin 0 auto
    width 100%
    height 180px

</style>



