<template>
    <transition name="fade">
        <div style="padding:0; width:100%">
            <v-layout row v-for="ri in rowNumber" :key="ri">
                <v-flex xs4 v-for="ci in 3" :key="ci" v-if="exist(ri, ci)"
                    class="text-xs-center" style="padding-top:10px;"
                    @click="openPage(ri, ci)"
                    :class="ci===3 ? 'borderB' : 'borderBR'">
                    <v-badge color="red" overlap v-model="pages[(ri-1)*3+(ci-1)].msgNumber>0">
                        <span slot="badge">{{pages[(ri-1)*3+(ci-1)].msgNumber}}</span>
                        <div class="grid-cell"
                            :class="pages[(ri-1)*3+(ci-1)].isValid ? 'image' : 'grayfilter image'"
                            :style="{'background-image': 'url(~/static/img/' + pages[(ri-1)*3+(ci-1)].image + ')'}">
                        </div>
                    </v-badge>
                    <p>{{pages[(ri-1)*3+(ci-1)].title}}</p>
                </v-flex>
            </v-layout>
        </div>
    </transition>
</template>

<script>
import EventBus from '@/event-bus';

export default {
    props: ['pages'],
    computed: {
        rowNumber() {
            return Math.ceil(this.pages.length / 3);
        }
    },
    methods: {
        exist(ri, ci) {
            return (ri-1)*3+(ci-1) < this.pages.length;
        },
        openPage(ri, ci){
            if(this.pages[(ri-1)*3+(ci-1)].isValid) {
                this.$router.push(this.pages[(ri-1)*3+(ci-1)].path);
            }
            else {
                EventBus.$emit('toast:show', '该功能尚未完成开发!');
            }
        }
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom


.app-mask-wrapper
    width 100%
    height 100%
    z-index $app-mask-zindex
    background $app-mask-bgcolor
    opacity $app-mask-opacity
    position fixed
    top 0
    left 0

    &.fade-enter,
    &.fade-leave-active
        opacity 0
        transition opacity $app-mask-duration

    &.fade-leave,
    &.fade-enter-active
        transition opacity $app-mask-duration

.borderBR
    border-bottom 1px solid #ddd
    border-right 1px solid #ddd

.borderB
    border-bottom 1px solid #ddd

.grid-cell
    margin auto
    margin-top 10px
    height 64px
    width 64px
    text-align center

.image
    background-repeat no-repeat
    background-position center center
    background-size 100% 100%
    
.grayfilter 
    filter gray
    -webkit-filter grayscale(1)
    filter grayscale(1)
  
p
    text-align center
    margin 5px 0
</style>



